}

.card-title {
  font-weight: 500 !important;
  text-align: center;
}

.card-data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.delete-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
